/**
 * ÂãïÁîª„Çø„Ç§„Éà„É´„ÇíË°®Á§∫Áî®„Å´„Éï„Ç©„Éº„Éû„ÉÉ„Éà„Åô„Çã
 * 
 * @param title - ÂÖÉ„ÅÆ„Çø„Ç§„Éà„É´Ôºà‰æã: "1-1_Ëµ∑Ê•≠„ÅÆÁßëÂ≠¶Êñ∞Ë£ÖÁâà„Å®„ÅØ"Ôºâ
 * @param displayOrder - ÈÄ£Áï™Ôºà‰æã: 1Ôºâ
 * @returns „Éï„Ç©„Éº„Éû„ÉÉ„ÉàÊ∏à„Åø„Çø„Ç§„Éà„É´Ôºà‰æã: "1 Ëµ∑Ê•≠„ÅÆÁßëÂ≠¶Êñ∞Ë£ÖÁâà„Å®„ÅØ"Ôºâ
 */
export function formatVideoTitle(title: string, displayOrder?: number | null): string {
  if (!title) return '';
  
  // Áï™Âè∑ÈÉ®ÂàÜÔºà1-1_, 2-3_„Å™„Å©Ôºâ„ÇíÂâäÈô§„Åó„Å¶Êú¨Êñá„ÅÆ„ÅøÂèñÂæó
  const textPart = title.replace(/^(\d+(?:-\d+)?)_/, '');
  
  // displayOrder„Åå„ÅÇ„Çå„Å∞ÈÄ£Áï™„Çí‰ªò„Åë„Çã
  if (displayOrder) {
    return `${displayOrder} ${textPart}`;
  }
  
  // displayOrder„Åå„Å™„ÅÑÂ†¥Âêà„ÅØÂÖÉ„ÅÆÂ§âÊèõÔºà„Ç¢„É≥„ÉÄ„Éº„Çπ„Ç≥„Ç¢„Çí„Çπ„Éö„Éº„Çπ„Å´Ôºâ
  return title.replace(/^(\d+(?:-\d+)?)_/, '$1 ');
}
